.modal {
  display: none;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.75); }

.modal-content {
  font-family: "Montserrat";
  background-color: #ebebeb;
  width: 35%;
  padding: 2.3rem 4rem;
  position: relative; }
  .modal-content:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background-color: #00aeef; }
  @media screen and (max-width: 1200px) {
    .modal-content {
      width: 50%; } }
  @media screen and (max-width: 992px) {
    .modal-content {
      width: 60%; } }
  @media screen and (max-width: 600px) {
    .modal-content {
      width: 90%;
      padding: 2.3rem 2rem; } }

.modal-header h2 {
  color: #313131;
  margin-top: 0; }

.modal-header .close {
  position: absolute;
  cursor: pointer;
  right: -2rem;
  top: 0;
  color: white;
  font-size: 3rem;
  line-height: 1.5rem; }
  @media screen and (max-width: 600px) {
    .modal-header .close {
      right: 0;
      top: -2rem; } }

.modal-body .form-group {
  margin-bottom: 1rem; }

.modal-body .form-control {
  display: block;
  font-family: "OpenSans";
  width: 100%;
  border: 1px solid #cccccc;
  border-radius: 0;
  padding: 1rem; }
  .modal-body .form-control:focus {
    outline: 1px solid #80bdff; }

.modal-body textarea.form-control {
  resize: none; }

.modal-body input[type="submit"] {
  cursor: pointer;
  margin: 0 auto;
  padding: 1rem 0;
  display: block;
  background-color: #17ad60;
  color: white;
  border-radius: 0;
  border: none;
  width: 60%;
  font-weight: bold;
  text-transform: uppercase;
  outline: none;
  display: block; }
